'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import { Button } from '../ui/Button';
import { createClient } from '@/lib/supabase/client';
import { useRouter } from 'next/navigation';

export const HeaderDynamic: React.FC = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [user, setUser] = useState<any>(null);
  const [profile, setProfile] = useState<any>(null);
  const router = useRouter();
  const supabase = createClient();

  useEffect(() => {
    const checkUser = async () => {
      const { data: { user } } = await supabase.auth.getUser();
      setUser(user);

      if (user) {
        const { data: profileData } = await supabase
          .from('profiles')
          .select('*')
          .eq('id', user.id)
          .single();
        setProfile(profileData);
      }
    };

    checkUser();

    const { data: { subscription } } = supabase.auth.onAuthStateChange((event, session) => {
      setUser(session?.user ?? null);
      if (!session?.user) {
        setProfile(null);
      }
    });

    return () => subscription.unsubscribe();
  }, []);

  const handleLogout = async () => {
    await supabase.auth.signOut();
    router.push('/');
  };

  const getDashboardLink = () => {
    if (profile?.role === 'researcher' || profile?.role === 'admin') {
      return '/studio/dashboard';
    }
    return '/platform/feed';
  };

  return (
    <header className="sticky top-0 z-50 border-b border-[color:var(--border-soft)] bg-[color:var(--surface)]/95 backdrop-blur-sm">
      <nav className="container mx-auto px-4 md:px-6 lg:px-8">
        <div className="flex items-center justify-between h-16 md:h-20">
          <Link href="/" className="text-2xl md:text-3xl font-bold text-[color:var(--text-primary)]">
            Conscience
          </Link>
          
          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center gap-6 lg:gap-8">
            <Link href="/" className="text-[color:var(--text-primary)] hover:text-[color:var(--coral)] transition-colors">
              Início
            </Link>
            <Link href="/pricing" className="text-[color:var(--text-primary)] hover:text-[color:var(--coral)] transition-colors">
              Preços
            </Link>
            
            {user ? (
              <>
                <Link href={getDashboardLink()} className="text-[color:var(--text-primary)] hover:text-[color:var(--coral)] transition-colors">
                  Dashboard
                </Link>
                <div className="flex items-center gap-3">
                  <div className="text-right">
                    <p className="text-sm font-semibold text-[color:var(--text-primary)]">{profile?.full_name}</p>
                    <p className="text-xs text-[color:var(--text-muted)]">{profile?.subscription_tier || 'free'}</p>
                  </div>
                  <button
                    onClick={handleLogout}
                    className="rounded-full border border-[color:var(--border-soft)] px-4 py-2 text-sm font-semibold text-[color:var(--text-primary)] hover:bg-[color:var(--surface-muted)] transition-colors"
                  >
                    Sair
                  </button>
                </div>
              </>
            ) : (
              <>
                <Link href="/auth/login">
                  <Button variant="outline" size="sm">Entrar</Button>
                </Link>
                <Link href="/auth/signup">
                  <Button size="sm">Cadastrar</Button>
                </Link>
              </>
            )}
          </div>
          
          {/* Mobile Menu Button */}
          <button
            className="md:hidden p-2 text-[color:var(--text-primary)]"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            aria-label="Toggle menu"
            aria-expanded={isMenuOpen}
          >
            <svg
              className="w-6 h-6"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              {isMenuOpen ? (
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              ) : (
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              )}
            </svg>
          </button>
        </div>
        
        {/* Mobile Menu */}
        {isMenuOpen && (
          <div className="md:hidden pb-4 space-y-4">
            <Link
              href="/"
              className="block py-2 text-[color:var(--text-primary)] hover:text-[color:var(--coral)] transition-colors"
              onClick={() => setIsMenuOpen(false)}
            >
              Início
            </Link>
            <Link
              href="/pricing"
              className="block py-2 text-[color:var(--text-primary)] hover:text-[color:var(--coral)] transition-colors"
              onClick={() => setIsMenuOpen(false)}
            >
              Preços
            </Link>
            
            {user ? (
              <>
                <Link
                  href={getDashboardLink()}
                  className="block py-2 text-[color:var(--text-primary)] hover:text-[color:var(--coral)] transition-colors"
                  onClick={() => setIsMenuOpen(false)}
                >
                  Dashboard
                </Link>
                <div className="pt-2 border-t border-[color:var(--border-soft)]">
                  <p className="text-sm font-semibold text-[color:var(--text-primary)] mb-2">{profile?.full_name}</p>
                  <button
                    onClick={() => {
                      handleLogout();
                      setIsMenuOpen(false);
                    }}
                    className="w-full rounded-full border border-[color:var(--border-soft)] px-4 py-2 text-sm font-semibold text-[color:var(--text-primary)] hover:bg-[color:var(--surface-muted)] transition-colors"
                  >
                    Sair
                  </button>
                </div>
              </>
            ) : (
              <div className="flex flex-col gap-2 pt-2">
                <Link href="/auth/login" onClick={() => setIsMenuOpen(false)}>
                  <Button variant="outline" size="sm" className="w-full">Entrar</Button>
                </Link>
                <Link href="/auth/signup" onClick={() => setIsMenuOpen(false)}>
                  <Button size="sm" className="w-full">Cadastrar</Button>
                </Link>
              </div>
            )}
          </div>
        )}
      </nav>
    </header>
  );
};

